.product-list {
    padding: 3em;
    max-width: auto;
    max-height: auto;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(1, 1fr);
    display: grid;
    grid-gap: 50px;
    align-items: center;
    margin-bottom: 2em;
    background-color: whitesmoke;
}

.card img {
    max-width: 50%;
}

.card .text {
    padding-top: 0.5em;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
}

